.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.game-over {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    color: whitesmoke;
}

.game-over h3 {
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}

.game-over h4 {
    margin-top: 15px;
    text-transform: uppercase;
}

.game-over p {
    margin: 0 auto 15px;

}

.reset {
    cursor: pointer;
    background-color: whitesmoke;
    padding: 10px 30px;
    text-transform: uppercase;
    color: black;


}
